*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;

}

.App{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

}




.main-body{
    display: flex;
    height: auto;
    width: auto;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}

.avatar{
    margin-right: 20px;
}

.details-con{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.details-con > h2{
    /* font-family: 'Montserrat', sans-serif; */
    /* font-weight: 550;
    margin-bottom: 12px; */
    /* font-family: 'Poppins', sans-serif; */
    color: #2D354A;
			font-size: 1.5rem;
			font-weight: 600;
			text-align: center;
}

.details-con > .profession{
    /* font-size: 15px;
    color: #2C3E50;
    font-weight: 600;
    margin-bottom: 12px; */

    color: #7C8097;
			font-size: .9rem;
			font-weight: 300;
			text-align: center;
			padding-top: .5em;
			padding-bottom: .7em;
			letter-spacing: 1.5px;
			text-transform: uppercase;
}

.details-con > .location{
    /* font-size: 13px;
    color: #2C3E50;
    margin-bottom: 12px; */
    
    color: #080911;
    font-size: .8rem;
    font-weight: 300;
    text-align: center;
    margin-bottom: .5em;
}

.details-con > .slills {
    margin-bottom: 4px;

}

.details-con > .slills > span{
    font-size: .6rem;
    display: inline-block;
    background-color: #e5e7eb ;
    padding: 4px 8px;
    margin: 0px 8px 8px 0px;
    border-radius: 5px;
}

.details-con > .social-handle > a{
    margin: 0px 8px 8px 0px;
    text-decoration: none;
    color: #080911;

    /* display: flex;
    flex-direction: row;
    justify-content: flex-start;
    width: 100%; */
    
}



.social-handle > .MuiSvgIcon-root{
    cursor: pointer;
}

@media only screen and (min-width: 600px) {
    .details-con > h2{
        
                font-size: 2.4rem;
                
                
    }

    .details-con > .profession{
        
                font-size: 1.2rem;
    }

    .details-con > .location{
        
        font-size: 1rem;
    }
    .details-con > .slills > span{
        font-size: 1rem;
    }
    /* .details-con > .social-handle > a{
        font-size: 1rem;
    }
     */
  }

@media only screen and (max-width: 600px) {
    .main-body {
        flex-direction: column;
    }

    .details-con{
        align-items: center;
    }

    .avatar > .MuiAvatar-root{
        height: 260px;
        width: 260px;
        /* margin: 0px 24px 0px 0px; */
        margin-bottom: 24px;
    }
    
  }

