.pages {
    /* background-color: blueviolet; */
    height: 100vh;
    width: 100vw;
}


.page-head {
    padding-top: 3rem;
    text-align: center;
}

.page-head>h3 {
    margin: .5rem;
    padding: .5rem;
    font-size: 2.25rem;
}

.page-grid {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-gap: 5rem;
    gap: 5rem;
    margin: 0 2rem;
}


.card {
    /* margin: 10px; */
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    width: 100%;
}

.card-header img {
    width: 100%;
    height: 20rem;
    object-fit: cover;
}

.card-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 20px;
