.card {
    /* margin: 10px; */
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    width: 100%;
}

.card-header img {
    width: 100%;
    height: 18rem;
    object-fit: cover;
}

.card-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    /* min-height: 250px; */
}

.slills>span {
    font-size: .6rem;
    display: inline-block;
    background-color: #e5e7eb;
    padding: 4px 8px;
    margin: 0px 8px 8px 0px;
    border-radius: 5px;
}

.head-text{
    margin: .5rem;
}


@media only screen and (max-width: 600px) {

    .card-header img {
        height: 15rem;
    }
}