.container {
    position: fixed;
    /* background-color: bisque; */
    top: 0;
    left: 0;
    width: 100%;
    box-shadow: rgba(33, 35, 38, 0.1) 0px 10px 10px -10px;
    z-index: 10;
    backdrop-filter: blur(10px);
}

.link-cont{
    margin: 1rem;
    display: flex;
    justify-content: center;
}


.links{
    margin: .2rem 1rem;
    text-decoration: none;
    font-size: 1rem;
    color: #6a6a6a;
    cursor: pointer;
    font-weight: bold;
    text-transform: uppercase;
    word-spacing: 4px;
}